#calculator {
  background-color: #b796ca;
  height: 320px;
  width: 260px;
  margin: auto;
  margin-top: 180px;
  padding: 30px;
  border: 3px solid black;
  border-radius: 10px;
}
#calculator button {
  height: 33px;
  width: 50px;
  background-color: #dbcae5;
  margin-bottom: 25px;
  margin-right: 15px;
  border-radius: 10px;
 
  margin-top: 4px;
  color: black;
}
#calculator input {
  padding: 2px;
  height: 30px;
  width: 250px;
  text-align: end;
  margin-bottom: 10px;
  border: 2px solid black;
  border-radius: 5px;
}
#calculator .eq {
  width: 100px;
  border-radius: 40px;
}
