#login {
  background-color: #e4c5f7;
  height: 160px;
  width: 290px;
  padding: 70px;
  margin: auto;
  margin-top: 240px;
  border: 2px solid #000000;
  border-radius: 10px;
}
#login input {
  width: 250px;
  padding: 9px;
  border-radius: 5px;
  margin: 2px;
  margin: auto;
  margin-bottom: 10px;
  padding-right: 5px;
}

#login .required::after {
  content: " *";
  color: #6d5e75;
}
#login .welcome {
  padding: 6px;
}
#login input[type="button"] {
    background-color: #392642; color: white

}
#login button {
  background-color: #392642;
  color: white;
  width: 270px;
  height: 30px;
}