#page .card {
  background-color: #b796ca;
  display: inline-block;
  width: 220px;
  height: 280px;
  padding: 20px;
  margin-right: 30px;
  margin-bottom: 25px;
  margin-top: 30px;
  margin-left: 30px;
  /* border: 2px solid black; */
  border-radius: 6px;
  position: absolute;
  top: 40;
}
#page button {
  width: 50px;
  height: 20px;
  background-color:  #392642;
  margin-left: 80px;
  margin-top: 2px;
  color: whitesmoke;
  border: none;
}
#page button:hover {
  background-color: #392642;

  color: black;
  transition: 2s;
}
#page img {
  height: 150px;
  width: 150px;
  /* margin-top: 10px; */
  margin-left: 30px;
}
#page table {
  margin: auto;
  margin-left: 330px;
  /* margin-right: 50px; */
  margin-top: 40px;
  background-color: #b796ca;
  position: absolute;
  height: 310px;
  width: 600px;
  top: 100;
}
