#signupform {
    background-color: #E4C5F7;
    width: 270px;
    height: 250px;
    padding: 80px;
    margin: auto;
    margin-top: 240px;
    border:2px solid #000000;
    /* border-width:3px;  */
    border-radius: 10px;
  }
  #signupform input{
    width: 250px;
    padding:6px;
    margin-bottom: 30px;
    border: 1px solid black;
    
  }
  #signupform .required::after{
    content:" *";
    color:#6D5E75;
  }
  #signupform input[type="button"]{
  background-color: #392642; color: white
  }
  #signupform button{
    background-color: #392642;
    color: aliceblue;
    width: 260px;
    height: 35px;
  }